.homeBody {
	width: 1000px;
	height: 1000px;
	background: url(../assets/plaid.jpg);
	background-size: 300px;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 10px;
	padding: 20px;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.rowOne {
	width: 100%;
	height: 300px;
	background-color: #d28644;
	border-radius: 10px;
	padding: 10px;
	display: flex;
	flex-direction: row;
	align-items: left;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.welcome {
	width: 60%;
	height: 100%;
	border-radius: 10px;
	background-color: #f8d0ae;
	margin-right: 80px;
	border: dotted 5px white;
}

.welcome p {
	margin: 20px
}

.welcome i {
	font-family: "Meow Script", cursive;
	font-size: 25px;
	margin-right: 5px;
}

.welcome b {
	font-family: "Jolly Lodger", serif;
	font-size: 20px;
}