.App {
	width: 100%;
	height: auto;
	display: flex;
	flex-direction: column;
	font-family: "Delius", Georgia, sans-serif;
}

body {
	font-size: 20px;
	color:#241208;
	background-color: rgba(237, 204, 174, 0.75);
	background-blend-mode: overlay;
	background-image: url("./assets/thebells.jpg");
	background-size: 1px;
	background-attachment: fixed;
	padding: 0;
	margin: 0;
}

body h1 {
	margin: 10px;
	color: #693305;
	font-family: "Leckerli One", cursive;
	font-size: 50px;
}

body h2 {
	margin: 10px;
	color: #693305;
	font-family: "Leckerli One", cursive;
	font-size: 30px;
}