.navigation {
	position: fixed;
	width: 120px;
	height: 200px;
	bottom: 20px;
	left: 20px;
	background-color: #d28644;
	border: double 5px #674427;
	font-size: 14px;
	border-radius: 5px;
	text-align: left;
	display: flex;
	flex-direction: column;
	padding: 5px;
	z-index: 1000;
}

.navTop {
	color: #eadbc6;
	font-family: 'Times New Roman', Times, serif;
	text-align: left;
	margin: 5px;
	margin-left: 10px;
}

.navLink {
	border-bottom: 2px dotted #ffffff;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 5px;
}

.navLink a {
	margin-left: 10px;
	color: #ffffff;
}