.projectsBody {
	width: 1200px;
	height: 100%;
	background: url(../assets/plaid.jpg);
	background-size: 300px;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 10px;
	padding: 20px;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4)
}

.projectsContent {
	width: 70%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.intro {
	width: 100%;
	height: 100%;
	border-radius: 10px;
	background-color: #f8d0ae;
	margin: 10px;
	padding: 10px;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.categoryContent {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;

}

.categoryInfo {
	width: 100%;
	height: 100%;
	border-radius: 10px;
	background-color: #f8d0ae;
	margin: 10px;
	padding: 10px;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.projectTabs {
	width: 100%;
	height: 40px;
	background-color: #d28644;
	padding: 10px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.projectTab {
	width: 100%;
	height: 100%;
	border-radius: 10px;
	background-color: #f8d0ae;
	margin: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: "Leckerli One", cursive;
}

.projectTab a {
	color: #693305;
}

.project {
	width: 100%;
	height: 100%;
	border-radius: 10px;
	background-color: #d28644;
	padding: 10px;
}